header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
text-align: center;
height: 100%;
position: relative;
}

/*================================= CTA ==========================*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;

}


/*================================= Socials ==========================*/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}


/*================================= ME ==========================*/

.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: 75%;
    transform: translate(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.you{
    background: linear-gradient(var(--color-primary-variant), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: 25%;
    transform: translate(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 5rem 5rem 5rem;
}

